import{ ReactElement } from 'react'
import { domain } from 'src/api'

const NavBarFooter = (): ReactElement => {

    return (
        <footer className="flex-row navbar-footer">
            <a href={`${domain}/rules.php`}></a>
            <span className="separator">|</span>
            <a href={`${domain}/about.php`}></a>
            <span className="separator">|</span>
            <b><a href={`${domain}/ad.php`}>  </a>  </b>
            <span className="separator">|</span>
            <a href={`${domain}/find.php`}></a>
            <span className="separator">|</span>
            <a rel="nofollow" href={`${domain}/sections_list.php`}></a>
            <span className="separator">|</span>
            <a rel="nofollow" href={`${domain}/rating.php`}></a>
            <span className="separator">|</span>
            <a href="http://kb.mista.ru"> </a>
            <span className="separator">|</span>
            <a href="http://wiki.mista.ru">- (2)</a>
            <span className="separator">|</span>
            <a rel="nofollow" href="http://m.mista.ru/"></a>
            <span className="separator">|</span>
            <a href={`${domain}/archive.php`}></a>
            <span className="separator">|</span>
            <a href={`${domain}/moders.php`}></a>
            <span className="separator">|</span>
            <a href={`${domain}/users_gallery.php`}></a>
            <span className="separator">|</span>
            <a href={`${domain}/ban_list.php`}></a>
            <span className="separator">|</span>
            <span>18+</span>
        </footer>
    )
}

export default NavBarFooter;