import { FC } from 'react'

type IProps = {
  onUpdateClick: (e: React.MouseEvent) => void,
  isLoading: boolean
}

const TableHeader: FC<IProps> = ({ isLoading, onUpdateClick }): React.ReactElement => {

  return (
    <div className="th" style={{ position: "sticky", top: "39px" }}>
      <div style={{ letterSpacing: "-1px" }}></div>
      <div></div>
      <div></div>
      <div>Re</div>
      <div></div>
      <div><span style={{ cursor: "pointer" }} title=" " onClick={onUpdateClick}>{isLoading ? "" : ""}</span></div>
    </div>
  )  
}

export default TableHeader;